import axios from 'axios'
export default {
  name: 'projectFrameFramework',
  data () {
    return {
      manager: '',
      mission: [],
      module: [],
      leader: ''
    }
  },
  methods: {
    getFramework () {
      axios.get(this.url + '/project/frame/' + sessionStorage.getItem('pid'))
        .then(this.getFrameworkSucc)
    },
    getFrameworkSucc (res) {
      if (res.status !== 200) {
        alert('该项目暂无架构！')
        this.$router.push('./frame')
      } else {
        res = res.data
        this.manager = res.managerName
        this.module = res.modules
        this.mission = res.modules.missions
      }
    },
    handleChange (val) {
      console.log(val)
    }
  },
  mounted () {
    this.getFramework()
  }
}



